.App {
	color: #333;
	text-align: center;
	background-color: #ffc3369f;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: calc(10px + 2vmin);
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.container {
	display: flex;
	flex-direction: column;
	row-gap: 5px;
	align-items: center;
	position: relative;
}

.img {
	border: solid 3px #282727;
	border-radius: 6px;
	margin-top: 30px;
}

.likeBtn {
	display: flex;
	margin-top: 20px;
	/* width: 30%; */
	font-size: 24px;
	font-weight: bold;
	text-transform: uppercase;
	background-color: #ffffff00;
	border-radius: 5px;
	cursor: pointer;
	position: absolute;
	bottom: 24px;
	right: 10px;
	border: none;
	padding: 10px;
	border-radius: 50%;
}

.btn {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	width: 100%;
}

.likeBtn:hover {
	padding: 10px;
	border-radius: 50%;
	background-color: #ffc33680;
}

.icon {
	width: 28px;
}

.posts {
	display: flex;
	align-items: center;
	justify-content: space-between;
	column-gap: 32px;
	margin-bottom: 42px;
}

.user {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: left;
	padding-left: 12px;
}

.profile {
	border: solid 3px #333;
	border-radius: 50%;
	margin-right: 12px;
}

.logo {
	position: absolute;
	left: 30px;
	top: 30px;
}

/* ------------------------------------------------------- */

h2 {
	margin-bottom: 42px;
}

.input-container {
	display: flex;
	flex-direction: column;
	width: 100%;
	background-color: #ffd73692;
}

.mainWrapper {
	display: flex;
	flex-direction: column;
	row-gap: 5vh;
	justify-content: left;
}

.messageContent {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	min-height: 40vh;
}

.message {
	font-size: 28px;
	font-weight: 500;
}

.mainContent {
	display: inline-flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-left: 20px;
	min-height: 60vh;
	margin-bottom: 38px;
}

.subContent {
	display: inline-flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-left: 20px;
	min-height: 50vh;
	margin-bottom: 38px;
}

.imageHolder img {
	width: 50vw;
	height: 50vh;
	object-fit: contain;
	margin: 14px auto;
}

.subImageHolder img {
	width: 45vw;
	height: 45vh;
	object-fit: contain;
	margin: 14px auto;
}

.inputHolder {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 42px;
}

.uploadImg {
	cursor: pointer;
	background-color: #333;
	color: #eee;
	padding: 8px 14px;
	border-radius: 2px;
	text-transform: uppercase;
	font-weight: 600;
	font-size: 14px;
}

.uploadFileInput {
	width: 0;
	height: 0;
	opacity: 0;
	visibility: hidden;
}

.buttonHolder {
	display: flex;
	column-gap: 20px;
}

.button {
	cursor: pointer;
	padding: 12px 20px;
	background-color: #ff9d00;
	color: #444;
	font-size: 18px;
	font-weight: 500;
	border-style: none;
	border-radius: 3px;
	align-content: center;
}

.cancel {
	background-color: #888;
	color: #fff;
}

.hideSub {
	text-decoration: none;
}

.recentPredictions {
	margin-top: 30px;
	padding: 4px 20px;
	border-radius: 2px;
	border: dashed 2px #333;
	margin-bottom: 38px;
}

.recentImages {
	display: flex;
	column-gap: 14px;
	row-gap: 10px;
	flex-wrap: wrap;
}

.recentPrediction img {
	cursor: pointer;
	width: 240px;
	height: 200px;
	object-fit: contain;
	border-radius: 2px;
	border: solid #000 1px;
	background-color: #c9d40050;
	transition: ease-in 200ms;
}

.recentPrediction img:hover {
	background-color: #d4a20076;
	transition: ease-in 200ms;
}
